@import "sass/index";

.root {
  position: relative;

  & label {
    color: #{use(--template-input-text-color)} !important;
  }
  & > div > div {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid #323e3f;
    border-radius: 8px;
  }
}

.inputRoot {
  height: 40px;
}

.input {
  color: use(--template-input-text-color);
}

.coin {
  position: static;
}
