@import "sass/index";

.root {
  position: relative;
  height: 47px;
  width: 100%;

  top: 3px;
}

.input {
  color: use(--template-input-topLabel-color) !important;
}

.buttons {
  top: 0;
  right: 0;
  height: 100%;
  padding: 4px;

  display: flex;
  align-items: center;
  @media (max-width: 450px) {
    top: auto;
    width: 100%;
    justify-content: space-between;
  }
}

.coinImg {
  position: static;
}

.errorCoin {
  color: #fe4e4eb0 !important;
}

.button {
  height: 100%;
  min-width: 47px;
  background: #323e3f;
  border: none;
  margin-left: 1px;
  padding: 6px 16px !important;

  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
  font-family: "Gilroy";
  font-weight: bold;
  cursor: pointer;

  @media (max-width: 450px) {
    width: 100%;
  }
}

.leftButton {
  border-radius: 6px 0px 0px 6px;
}

.rightButton {
  border-radius: 0px 6px 6px 0px;
}
