.root {
  position: relative;
  margin: 0 auto;
  width: fit-content;
  transition: all 0.5s;
}

.mobile {
  margin-top: 160px;
  transform: scale(0.9);
}

.img {
  position: absolute;
}

.arm {
  z-index: 1;
  pointer-events: none;
  top: 0;
}

.fps {
  position: absolute;
  top: 0;
  left: 0;
}
