@import "sass/index";
@import "games/Roulette/game/style.module.scss";
@import "pages/Roulette/variables.scss";

.rootPanel {
  width: fit-content;

  position: absolute;
  top: 0;

  margin-top: #{$cellPanelMargin}px;
  transform-origin: 50% 50%;
  transition: all #{$cellRotateSpeedMs}ms;
  display: flex;
}

.rotated {
  transform: rotate(-90deg) scale(1);
  margin-top: 0;
}

.cellPanel {
  display: flex;
  flex-direction: column;
}

.cells {
  margin-top: #{$rouletteCellGap}px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: #{$rouletteCellGap}px;
  gap: #{$rouletteCellGap}px;
}

.cell {
  width: #{$rouletteCellSize}px;
  height: #{$rouletteCellSize}px;
  padding: 0;
  text-align: center;
}

.rowCell {
  & > p {
    color: use(--template-anyContent-colors-fourth);
  }
}

.zeroCell {
  width: 100%;
  height: #{$rouletteCellSize}px;
}
