@import "pages/Roulette/variables.scss";

@keyframes old {
  0% {
    max-height: 100%;
    transform: translate(0, 0);
  }

  100% {
    transform: translate(#{$rouletteCellSize + $rouletteCellGap}px, 0);
    max-height: #{$rouletteCellSize}px;
  }
}

@keyframes new {
  0% {
    transform: scaleY(0);
  }

  100% {
    transform: scaleY(1);
  }
}

.root {
  display: block;
  width: #{$rouletteCellSize}px;
  height: #{$rouletteCellSize * 3 + $rouletteCellGap * 3}px;
  position: relative;
}

.bigCell {
  width: #{$rouletteCellSize}px;
  height: 100%;
  max-height: 100%;
  padding: 0;
}

.oldAnimation {
  animation: old #{$longAnimationMSTime / 2}ms forwards;
}

.newAnimation {
  position: absolute;
  bottom: 0;
  transform-origin: 50% 100%;
  animation: new #{$longAnimationMSTime}ms forwards;
}

.mobileCell {
  height: #{$rouletteCellSize}px;
  bottom: auto;
  top: 0;
}
