@import "pages/Roulette/variables.scss";
@import "games/Roulette/game/style.module.scss";

.root {
  position: absolute;
  top: 60px;

  transition: all 0.5s;

  display: flex;
  height: #{$rouletteCellSize * 3 + $rouletteCellGap * 3}px;
}

.mobilePosition {
  left: 0;
  transform: translateY(#{$rouletteBoxSize * 1.5}px);
}

.bigCellWrapper {
  height: 100%;
  width: #{$rouletteCellSize}px;

  margin: 0 5px;
}

.bigCell {
  width: #{$rouletteCellSize}px;
  height: 100%;
  padding: 0;
}

.smallCellWrapper {
  height: 100%;
  width: #{$rouletteCellSize}px;

  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 5px;
  gap: 5px;
}

.smallCell {
  width: #{$rouletteCellSize}px;
  height: #{$rouletteCellSize}px;
  padding: 0;

  display: flex;
  align-items: center;
  justify-content: center;
}

.disable {
  display: none;
}

.mobileCell {
  height: #{$rouletteCellSize}px;
  bottom: auto;
  top: 0;
}
