@import "sass/index";

.root {
  display: flex;
  justify-content: space-between;

  border-radius: 8px;
  overflow: hidden;

  position: relative;
  background: use(--template-anyContent-colors-second, 0.9);
}

.chipBox {
  display: flex;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 1;

  & > svg {
    margin: 0 -1px;
    flex-shrink: 0;
  }
}

.rightArrow {
  transform: rotate(180deg);
}

.arrow {
  color: use(--template-anyContent-colors-third);

  transition: color 0.2s;
}

.button {
  background: use(--template-anyContent-colors-first);
  border: none;
  outline: none;
  cursor: pointer;
}

.buttonDisable {
  cursor: default;
  & > svg {
    opacity: 0.3;
  }
}

.chip {
  cursor: pointer;
  transform: scale(0.85);
  transition: transform 0.3s, opacity 0.3s;
}

.chipSelected {
  transform: scale(1);
}

.chipDisable {
  opacity: 0.3;
}
