$cellPanelMargin: 20;
$rouletteCellGap: 5;
$rouletteCellSize: 50;

$cellRotateSpeedMs: 500;

$redHover: linear-gradient(-90deg, #940007 0%, #c1000a 30%, #ff0000 100%);
$blackHover: linear-gradient(-90deg, #525252 0%, #ffffffbe 100%);
$lightBlackHover: linear-gradient(
  -90deg,
  #b0b0b0 0%,
  #6c6c6c 15%,
  #000000 100%
);
$greenHover: linear-gradient(
  -90deg,
  #37ff00 0%,
  rgba(55, 255, 0, 0.4) 0.01%,
  rgb(55, 255, 0)
);
$transparentHover: #00000050;

$longAnimationMSTime: 750;

:export {
  #{"cellPanelMargin"}: $cellPanelMargin;
  #{"rouletteCellGap"}: $rouletteCellGap;
  #{"rouletteCellSize"}: $rouletteCellSize;
}
