@import "sass/index";

$rouletteMobileSwipeWidth: 490;
$rouletteGameBlockPadding: 30;

:export {
  #{"rouletteMobileSwipeWidth"}: $rouletteMobileSwipeWidth;
  #{"rouletteGameBlockPadding"}: $rouletteGameBlockPadding;
}

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}

.gameBlock {
  background: #{use(--template-gameCell-wrapper)} url(/assets/logos/background-pasino-logo.svg)
    no-repeat;
  background-position: center 100px;
  background-blend-mode: difference;
  background-size: 90%;
  overflow: hidden;
  border-radius: 30px;
  padding: #{$rouletteGameBlockPadding}px;
  width: 100%;
  height: 100%;
  display: flex;

  font-weight: 500;

  & input {
    font-weight: 500;
  }

  @media (max-width: 600px) {
    padding: #{$rouletteGameBlockPadding}px 0;
  }
}

.statisticsWrapper {
  padding-top: 26px;
}

.gameWrapper {
  width: 100%;
  min-width: #{$rouletteMobileSwipeWidth}px;

  position: absolute;
  right: 0;
  transition: transform 0.4s;
}

.gameCell {
  width: 100%;
  position: relative;
}

.gameFooter {
  margin-top: 600px;
}
