$rouletteBoxSize: 270;

:export {
  #{"rouletteBoxSize"}: $rouletteBoxSize;
}

@keyframes roulette {
  100% {
    transform: rotate(-360deg);
  }
}

.img {
  position: absolute;
  opacity: 1;
  z-index: 0;
  transform: rotate(95deg);
}

.rouletteRoot {
  width: #{$rouletteBoxSize}px;
  height: #{$rouletteBoxSize}px;
  margin: 0;

  animation: roulette 7s linear infinite;

  & > canvas {
    width: #{$rouletteBoxSize}px !important;
    height: #{$rouletteBoxSize}px !important;

    z-index: 1;
    position: relative;
  }
}
