@import "pages/Roulette/variables.scss";

.root {
  //   width: 100%;
  //   height: 100%;
  border-radius: 6px;

  color: white;
  text-align: center;

  font-family: "Gilroy";
  font-size: 1rem;
  font-weight: bold;

  display: flex;
  align-items: center;
  justify-content: center;

  cursor: pointer;
  user-select: none;

  transition: border 0.3s, box-shadow 0.3s;
}

.red {
  background: linear-gradient(-90deg, #b6161d 0%, #f1252e 100%);

  &:hover {
    background: $redHover;
  }
}

.black {
  background: linear-gradient(
    -90deg,
    rgba(81, 81, 81, 0.3) 0%,
    rgba(120, 120, 120, 0.5) 100%
  );

  &:hover {
    background: $blackHover;
  }
}

.lightBlack {
  background: linear-gradient(
    -90deg,
    rgba(0, 0, 0, 0.9) 0%,
    rgba(0, 0, 0, 0.6) 100%
  );

  &:hover {
    background: $lightBlackHover;
  }
}

.transparent {
  border: 2px solid #444444;

  &:hover {
    background: $transparentHover;
  }
}

.green {
  background: linear-gradient(
    -90deg,
    rgba(103, 178, 82, 0) 0%,
    rgba(88, 206, 55, 0.4) 0.01%,
    #30b80b 100%
  );

  &:hover {
    background: $greenHover;
  }
}

.hover_red {
  background: $redHover;
}

.hover_black {
  background: $blackHover;
}

.hover_lightBlack {
  background: $lightBlackHover;
}

.hover_green {
  background: $greenHover;
}

.hover_transparent {
  background: $transparentHover;
}

.winCell {
  border: 1px solid #00e2ff;
  box-shadow: 0px 0px 0px 3px #00e2ff;
}

.text {
  margin: 0;
  padding: 0;
  transition: transform #{$cellRotateSpeedMs}ms;
}

.rotated {
  transform: rotate(90deg);
}

.disabled {
  pointer-events: none;
}

.chip {
  transition: transform #{$cellRotateSpeedMs}ms;
  position: absolute;
  z-index: 3;
}
