@import "sass/index";

$gridGap: 10;
$chipGridSize: minmax(290px, calc(40% - #{$gridGap / 3}px));
$buttonGridSize: minmax(200px, calc(25% - #{$gridGap / 3}px));
$betInputGridSize: minmax(260px, calc(35% - #{$gridGap / 3}px));

.root {
  padding-top: 15px;
  display: grid;
  grid-template-columns: $chipGridSize $buttonGridSize $betInputGridSize;
  align-items: end;
  grid-gap: #{$gridGap}px;
  gap: #{$gridGap}px;
}

.title {
  top: -1rem;
  left: 0;
  margin: 0;
  opacity: 0.8;
  margin-bottom: 7px;
  font-size: 1rem;
  color: use(--template-input-text-color);
  font-family: "Gilroy";
}

.coin {
  position: static;
  width: 26px;
  height: 26px;
  margin-bottom: -6px;
  margin-left: 3px;
}
