@import "sass/index";
@import "pages/Roulette/variables.scss";

.root {
  display: block;
  margin-top: #{$rouletteCellSize}px;
  position: absolute;
  top: 0;
  left: calc(-100% + #{$rouletteCellSize}px);
}

.cells {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "left center" "right center";
  grid-gap: #{$rouletteCellGap}px;
  margin-bottom: #{$rouletteCellGap}px;
}

.cellUnit {
  width: #{$rouletteCellSize}px;
  white-space: nowrap;
  color: use(--template-anyContent-colors-fourth);
}

.cellCenter {
  grid-area: center;
  height: #{($rouletteCellSize + $rouletteCellGap) * 4}px;
}

.cellLeft {
  grid-area: left;
}

.cellRight {
  grid-area: right;
}
