$switchBodyHeight: 14px;

.root {
  min-width: min-content;
  margin: 0 auto;
  width: 80%;
}

.button {
  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.135107);
  border-radius: 25px;

  font-family: "Gilroy";
  font-size: 1.375rem;
  text-transform: uppercase;
  color: #ffffff;
  letter-spacing: 1px;
  font-weight: bold;

  padding: 12px 25px;
  margin: 0 auto;
  display: block;

  min-width: 200px;
  max-width: 300px;
  width: 100%;

  line-height: 1.4;
  border-width: 1.5px;
  outline: none;
}

.switch {
  margin-bottom: 10px;
  &Track {
    height: 14px;
    width: 38px;
  }
  &Toggle {
    width: 20px;
    height: 20px;
  }
}
