@import "pages/Roulette/variables.scss";

@keyframes move {
  0% {
    max-height: 100%;
    transform: translateY(0);
  }

  100% {
    transform: translateY(#{$rouletteCellSize + $rouletteCellGap}px);
  }
}

.rootAnm {
  height: #{$rouletteCellSize * 3 + $rouletteCellGap * 3}px;
  width: #{rouletteCellSize}px;
  overflow: hidden;
}

.cell {
  width: #{$rouletteCellSize}px;
  height: #{$rouletteCellSize}px;
  padding: 0;

  display: flex;
  align-items: center;
  justify-content: center;
  margin: #{$rouletteCellGap / 2}px 0 #{$rouletteCellGap}px;

  // &:last-child {
  //   marginBottom: 0;
  // }
}

.animation {
  animation: move #{$longAnimationMSTime / 2}ms forwards;
}
